import React from "react";
import { Link } from "react-router-dom";

export default function Menu(props) {
  return (
    <div className="row" style={{ border: "2px solid gray" }}>
      <div className="col-2">
        <Link to="/"></Link>
      </div>
      <div className="col-2">
        <Link to="/SingUp"></Link>
      </div>
      <div className="col-2">
        <Link to="/Login"></Link>
      </div>
      <div className="col-2">
        <Link to="/AllProducts"></Link>
      </div>
      <div className="col-4">
        <Link to="/ManagerPage"></Link>
      </div>
    </div>
  );
}
