import React from "react";
import { Link } from "react-router-dom";
import { Consumer } from "../ContextAPI";

export default function AllProducts(props) {
  const trueOrFalse = () => {
    if (props.worker.forkliftTuck === false) {
      return "NO";
    } else {
      return "YES";
    }
  };

  return (
    <div
      style={{
        display: "flex",
        flexDirection: "column",
        alignItems: "center",
        justifyContent: "center",
      }}
    >
      <div
        style={{
          height: "100%",
          width: "400px",
          backgroundColor: "lightgray",
          borderRadius: "15px",
          justifyContent: "center",
          flexDirection: "column",
          justifyContent: "space-evenly",
        }}
      >
        <h1>
          Welcome:
          <br />
          {props.worker.workerName}
        </h1>
        <div
          style={{
            textAlign: "left",
            display: "flex",
            flexDirection: "column",
            justifyContent: "space-around",
            margin: "20px",
          }}
        >
          <p style={{ textDecoration: "underline" }}>Details:</p>
          <p>Full Name: {props.worker.workerName}</p>
          <p>NO.:{props.worker.workerNumber} </p>
          <p>Forklift truck license:{trueOrFalse()}</p>
        </div>
        <div></div>

        <h4>List of products:</h4>

        <div>
          {props.products.map((e, i) => {
            if (!e.inPlace) {
              return (
                <div>
                  <div
                    style={{
                      borderWidth: "1px",
                      borderStyle: "solid",
                      padding: "10px",
                      width: "350px",
                      position: "relative",
                      left: "27px",
                    }}
                  >
                    <div style={{ textAlign: "left" }}>
                      <p>
                        <strong>NO.:</strong>
                        {`${e.type}`}
                      </p>
                      <br />
                      <p>
                        {" "}
                        <strong>Product Name:</strong>
                        {`${e.color}`}
                      </p>
                      <br />
                      <p>
                        {" "}
                        <strong>Need forklift truck:</strong>
                        {`${e.forkliftTuck}`}
                      </p>

                      <Consumer>
                        {(val) => {
                          return (
                            <button
                              style={{
                                borderRadius: "10px",
                                position: "relative",
                                left: "220px",
                                width: "100px",
                                backgroundColor: "grey",
                                fontSize: "20px",
                                color: "white",
                              }}
                              onClick={() => {
                                val.deleteProducts(i);
                                val.productsInPlace(i);
                                val.inPlaceOfWorkers(i);
                              }}
                            >
                              Update
                            </button>
                          );
                        }}
                      </Consumer>
                    </div>
                  </div>
                </div>
              );
            } else return <div></div>;
          })}
        </div>
        <Link to="/">
          <button
            style={{
              borderRadius: "10px",
              position: "relative",
              left: "130px",
              width: "100px",
              backgroundColor: "grey",
              fontSize: "20px",
              color: "white",
              margin: "30px",
            }}
          >
            Log out
          </button>
        </Link>
      </div>
    </div>
  );
}
