import React from "react";
import { Link } from "react-router-dom";

export default function ManagerPage(props) {
  const workerMap = props.workers.map((worker) => {
    return (
      <tr key={worker._id}>
        <td>{worker.noNumber}</td>
        <td>{worker.fullName}</td>
        <td>{worker.visitNumber}</td>
        <td>{worker.productsInPlace}</td>
      </tr>
    );
  });

  return (
    <div>
      <div style={{ backgroundColor: "lightgray" }}>
        <h1>Manager</h1>
        <table className="table">
          <thead>
            <tr>
              <th scope="col">NO</th>
              <th scope="col">FullName</th>
              <th scope="col">Counter</th>
              <th scope="col">Number of products</th>
            </tr>
          </thead>
          <tbody>{workerMap}</tbody>
        </table>
        <Link to="/">
          <button
            style={{
              borderRadius: "10px",
              position: "relative",
              left: "700px",
              width: "100px",
              backgroundColor: "grey",
              fontSize: "20px",
              color: "white",
              margin: "20px",
            }}
          >
            Log Out
          </button>
        </Link>
      </div>
    </div>
  );
}
