import "./App.css";
import "bootstrap/dist/css/bootstrap.min.css";
import React, { useState } from "react";
import { HashRouter as Router, Switch, Route } from "react-router-dom";
import { Provider } from "./ContextAPI";
import Register from "./components/register";
import Menu from "./components/menu";
import SignUp from "./components/signUp";
import Login from "./components/login";
import AllProducts from "./components/AllProducts";
import ManagerPage from "./components/managerPage";
import { Link } from "react-router-dom";

export default function App(props) {
  const [workers, setWorkers] = useState([]);
  const [products, setProducts] = useState([
    { inPlace: false, type: "11122", color: "Green Box", forkliftTuck: false },
    { inPlace: false, type: "22554", color: "Green Box", forkliftTuck: false },
    { inPlace: false, type: "66698", color: "Blue Box", forkliftTuck: true },
    { inPlace: false, type: "78544", color: "Red Box", forkliftTuck: false },
    { inPlace: false, type: "69875", color: "Red Box", forkliftTuck: false },
  ]);
  const [productInPlace, setProductInPlace] = useState([]);
  const [workerCounter, setWorkerCounter] = useState([]);
  // const [managerWorker, setManagerWorker] = useState([]);

  const handleInputs = (no, name) => {
    if (no != "" || name != "") {
      //if error is not an empty string
      alert("Please check errors");
    }
  };

  // create a worker user
  const handleAddWorker = (
    noNumber,
    fullName,
    forkliftTuck,
    visitNumber,
    productsInPlace
    // numberOfProductInPlace
  ) => {
    if (
      workers.some((e, i) => {
        return e.fullName === fullName;
      })
    ) {
      alert("User name already been used, please try another name.");
      <Link to="/">
        <button>back</button>
      </Link>;
    } else {
      workers.productsInPlace = workerCounter.productsInPlace;
      setWorkers([
        {
          visitNumber: 0,
          noNumber: noNumber,
          fullName: fullName,
          forkliftTuck: forkliftTuck,
          productsInPlace: 0,
        },
        ...workers,
      ]);
    }
  };

  console.log(workers);
  const handleVisitNumber = (login) => {
    let newWorkers = [...workers];
    for (let i = 0; i < workers.length; i++) {
      if (workers[i].noNumber === login) {
        setWorkerCounter({
          workerNumber: workers[i].noNumber,
          workerName: workers[i].fullName,
          forkliftTuck: workers[i].forkliftTuck,
          workerVisitNumber: workers[i].visitNumber++,
          workerVisitNumber: workers[i].visitNumber,
          productsInPlace: workers[i].productsInPlace,
        });
      }
    }
    setWorkers(newWorkers);
  };

  const handleProductsInPlace = (i) => {
    if (products[i].inPlace === true) {
      workerCounter.productsInPlace = workerCounter.productsInPlace + 1;
    }
  };

  const handleProductsInPlaceOfWorkers = (i) => {
    for (let e = 0; e < workers.length; e++) {
      // workers[e].productsInPlace = 0;
      if (products[i].inPlace === true) {
        workers[e].productsInPlace++;
        return;
      }
    }
  };
  const [managerLogin, setManagerLogin] = useState(99999);
  const handleManagerLogin = (login) => {
    if (managerLogin === login) {
      return (
        <Link to="/ManagerPage">
          <button>Enter</button>
        </Link>
      );
    }
  };

  const handleDeleteProducts = (i) => {
    if (
      workerCounter.forkliftTuck === false &&
      products[i].forkliftTuck === false
    ) {
      products[i].inPlace = true;
      setProducts([...products]);
    } else if (
      workerCounter.forkliftTuck === "true" &&
      products[i].forkliftTuck === true
    ) {
      products[i].inPlace = true;
      setProducts([...products]);
    } else if (
      workerCounter.forkliftTuck === "true" &&
      products[i].forkliftTuck === false
    ) {
      products[i].inPlace = true;
      setProducts([...products]);
    } else {
      alert("A forklift license is required");
    }
  };

  return (
    <div className="App">
      <Provider
        value={{
          checkInputs: handleInputs,
          addWorker: handleAddWorker,
          workers: workers,
          products: products,
          deleteProducts: handleDeleteProducts,
          productsInPlace: handleProductsInPlace,
          inPlaceOfWorkers: handleProductsInPlaceOfWorkers,
        }}
      >
        <Router>
          <Menu />
          <Switch>
            <Route
              exact
              path="/"
              component={() => {
                return (
                  <Register
                    workers={workers}
                    products={products}
                    worker={workerCounter}
                  />
                );
              }}
            />
            <Route
              exact
              path="/SingUp"
              component={() => {
                return <SignUp workers={workers} products={products} />;
              }}
            />
            <Route
              exact
              path="/Login"
              component={() => {
                return (
                  <Login
                    workers={workers}
                    products={products}
                    visitNumber={handleVisitNumber}
                    worker={workerCounter}
                    managerLogin={handleManagerLogin}
                  />
                );
              }}
            />
            <Route
              exact
              path="/AllProducts"
              component={() => {
                return (
                  <AllProducts
                    products={products}
                    worker={workerCounter}
                    workers={workers}
                  />
                );
              }}
            />
            <Route
              exact
              path="/ManagerPage"
              component={() => {
                return (
                  <ManagerPage
                    products={products}
                    worker={workerCounter}
                    workers={workers}
                  />
                );
              }}
            />
          </Switch>
        </Router>
      </Provider>
    </div>
  );
}
