import React, { useState } from "react";
import { Link } from "react-router-dom";
import { Consumer } from "../ContextAPI";

export default function SignUp(props) {
  const [userMessageNO, setUserMessageNO] = useState("");
  const [userMessageFullName, setUserMessageFullName] = useState("");
  const [inValidNo, setInValidNo] = useState("");
  const [inValidName, setInValidName] = useState("");
  const [forkliftTuck, setForkliftTuck] = useState(false);

  const handleNoInput = (element) => {
    if (element.target.value.length > 5) {
      setInValidNo(element.target.value);
      setUserMessageNO("NO mast be lese then 5 numbers");
    } else if (
      (element.target.value.charAt(0) >= "a" &&
        element.target.value.charAt(0) <= "z") ||
      (element.target.value.charAt(0) >= "A" &&
        element.target.value.charAt(0) <= "Z")
    ) {
      setUserMessageNO("NO mast be numbers only");
    } else {
      setInValidNo(element.target.value);
      setUserMessageNO("");
    }
  };

  const handleNameInput = (element) => {
    if (element.target.value.length < 5) {
      setInValidName(element.target.value);
      setUserMessageFullName("FullName mast be more then 5 letters");
    } else if (!isNaN(Number(element.target.value))) {
      setInValidName(element.target.value);
      setUserMessageFullName("FullName mast be only letters");
    } else if (element.target.value.indexOf(" ") == -1) {
      setInValidName(element.target.value);
      setUserMessageFullName(
        "FullName mast include space between name and last name"
      );
    } else {
      setInValidName(element.target.value);
      setUserMessageFullName("");
    }
  };

  const validate = (userMessageNO, userMessageFullName) => {
    if (userMessageNO === "" && userMessageFullName === "") {
      //if error is not an empty string
      return false;
    } else {
      return true;
    }
  };

  const handleRadio = (event) => {
    const forkliftTuck = event.currentTarget.value === "true" ? true : false;
    console.log("handle", forkliftTuck);
    setForkliftTuck(event.currentTarget.value);
  };

  return (
    <div
      style={{
        display: "flex",
        flexDirection: "column",
        alignItems: "center",
        justifyContent: "center",
      }}
    >
      <div
        style={{
          height: "500px",
          width: "400px",
          backgroundColor: "lightgray",
          borderRadius: "15px",
          justifyContent: "center",
          flexDirection: "column",
          justifyContent: "space-evenly",
        }}
      >
        <h1 style={{ margin: "30px" }}>Sign Up</h1>
        <div className="row" style={{ margin: "30px" }}>
          <label className="col-3" htmlFor="no">
            NO.
          </label>
          <input
            className="col-6"
            onChange={handleNoInput}
            type="text"
            // maxLength="5"
            pattern="[0-9]*"
          />
        </div>
        <label style={{ color: "red" }}>{userMessageNO}</label>
        <br />
        <div className="row" style={{ margin: "30px" }}>
          <label className="col-3" htmlFor="fullName">
            FullName:
          </label>
          <input className="col-6" onChange={handleNameInput} type="text" />
        </div>
        <label style={{ color: "red" }}>{userMessageFullName}</label>
        <br />
        Forklift tuck:
        <label>
          <input
            type="radio"
            name="forkliftTuck"
            value={true}
            checked={forkliftTuck === true}
            onChange={handleRadio}
          />
          Yes
        </label>
        <label>
          <input
            type="radio"
            name="forkliftTuck"
            value={false}
            checked={forkliftTuck === false}
            onChange={handleRadio}
          />
          No
        </label>
        <br />
        <Link to="/">
          <button>back</button>
        </Link>
        <Consumer>
          {(val) => {
            return (
              <Link to="/">
                <button
                  type="button"
                  disabled={validate(userMessageNO, userMessageFullName)}
                  onClick={() => {
                    val.checkInputs(userMessageNO, userMessageFullName);
                    val.addWorker(inValidNo, inValidName, forkliftTuck);
                  }}
                >
                  Create
                </button>
              </Link>
            );
          }}
        </Consumer>
      </div>
    </div>
  );
}
