import React, { useState } from "react";
import { Link } from "react-router-dom";

export default function Login(props) {
  const [login, setLogin] = useState("");
  const [managerLogin, setManagerLogin] = useState("99999");

  const showButton = () => {
    if (
      props.workers.some((worker) => {
        return worker.noNumber === login;
      })
    ) {
      return (
        <Link to="/AllProducts">
          <button
            onClick={() => {
              props.visitNumber(login);
            }}
          >
            Enter
          </button>
        </Link>
      );
    } else if (managerLogin === login) {
      return (
        <Link to="/ManagerPage">
          <button>Enter</button>
        </Link>
      );
    } else {
      return (
        <Link to="/">
          <button>Enter</button>
        </Link>
      );
    }
  };

  const onChangeLogin = (element) => {
    if (element.target.value.length > 0) {
      setLogin(element.target.value);
    }
  };

  return (
    <div
      style={{
        display: "flex",
        flexDirection: "column",
        alignItems: "center",
        justifyContent: "center",
      }}
    >
      <div
        style={{
          height: "500px",
          width: "400px",
          backgroundColor: "lightgray",
          borderRadius: "15px",
          justifyContent: "center",
          flexDirection: "column",
          justifyContent: "space-evenly",
        }}
      >
        <h1>login</h1>
        <p>
          NO. <input onChange={onChangeLogin} type="text" />
        </p>
        <br />

        <div>
          <div>
            <Link to="/">
              <button>back</button>
            </Link>
            {showButton()}
          </div>
        </div>
      </div>
    </div>
  );
}
